<template>
    <div>
        <g-appbar
            title="한국전력공사  T.B.M" 
            background="#f8f8fa"
            :left="true" :right="true"
        />
        <div class="g-area-top">
            <div class="d-flex align-center">
                <p class="g-step-now">{{ step }}</p>
                <div class="g-step-bar"></div>
                <p class="g-step">{{ totalStep }}</p>
            </div>
            <p class="s-title">
                안전장구 착용을 <br/>
                확인해 주세요.
            </p>
        </div>
        <div class="g-progress">
            <v-progress-linear 
                :model-value="step"
                :buffer-value="progress"
                color="#7285f3"
                background-opacity="1"
            ></v-progress-linear>
        </div>
        <div class="g-area-middle">
            <div class="g-voice">
                <div class="s-notice-box">
                    <img class="s-notice-icon" src="@/assets/images/icon/i_speaker.png"/>
                    <p>
                        아래의 <span class="font_bold">전달 사항</span>을 소리 내어 
                        <span class="font_bold"> 말씀해 주세요.</span>
                    </p>
                </div>
                <p class="s-content">
                    개인별 
                    <span :class="textStyle(operatorInfo[0].check)">안전장구</span>
                    는 생명과 직결되는 
                    <span :class="textStyle(operatorInfo[1].check)">중요한 요소</span>
                    입니다.<br/> 
                    <span :class="textStyle(operatorInfo[0].check)">안전장구</span>
                    는 
                    <span :class="textStyle(operatorInfo[2].check)">꼭 착용</span>
                    해 주세요.
                </p>
            </div> 
            <div class="g-fixed-voice-btn">
                <div class="g-fixed-voice-gradation">
                    <img class="g-fixed-voice-gradation" src="@/assets/images/voice/i_gradation_top.png"/>
                </div>
                <VoiceRecorderHidden 
                    ref="VoiceRecorder"
                    @checkAnalysisResult="checkAnalysisResult"
                    @midResultState="midResultState"
                />
                <basic-btn 
                    :state="voiceState"
                    @onClick="onClickNext"
                />
                <div v-if="voiceState === 'replay'">
                    <next-btn @onClick="onForceNext"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BasicBtn from '@/components/button/BasicBtn.vue'
import VoiceRecorderHidden from '@/components/common/VoiceRecorderHidden.vue'
import NextBtn from '@/components/button/NextBtn.vue'

export default {
    name: 'Step01',
    components: { BasicBtn, VoiceRecorderHidden, NextBtn },
    data() {
        return {
            step: 3,
            totalStep: 6,
            operatorInfo: [
                { index: 1, label: '안전장구', check: false, },
                { index: 2, label: '중요한 요소', check: false },
                { index: 3, label: '꼭 착용', check: false },
            ],
            searchKeywords: [
                { matchIndex: 1, texts: ['안전장구', '안전장군', '안전잔구', '안전장관', '운전장고', '안전잔고', '안전당구', '안전장고'] },
                { matchIndex: 2, texts: ['중요한요소'] },
                { matchIndex: 3, texts: ['꼭착용'] },
            ],
            voiceState: 'start',
        }
    },
    computed: {
        progress() {
            return Number((this.step) / (this.totalStep)) * 100;
        },
        allConfirm() {
            return this.voiceState === 'finish'
        }
    },
    methods: {
        onForceNext() {
            this.$router.push('/TBM/Step04')
        },
        onClickNext() {
            if (this.allConfirm) {
                this.$router.push('/TBM/Step04')
            } else if (this.voiceState === 'replay') {
                this.$refs.VoiceRecorder.startRecording()
            }
        },
        checkAnalysisResult(result) {
            const audio = result.replace(/\s/g, '');

            for (const keyword of this.searchKeywords) {
                for (const text of keyword.texts) {
                    if (audio.includes(text)) {
                        for (const operator of this.operatorInfo) {
                            if (operator.index === keyword.matchIndex) {
                                operator.check = true
                                break
                            }
                        }
                    }
                }
            }
        },
        midResultState(result) {
            if (result === 'finish') {
                const allChecked = this.operatorInfo.every((operator) => operator.check);
                if (!allChecked) {
                    result = 'replay'
                    this.$refs.VoiceRecorder.changeState(result)
                }
            }
            this.voiceState = result
        },
        textStyle(check) {
            return check ? "color_primary_4 font_extra_bold" : "color_primary_4"
        }
    }
}
</script>

<style scoped lang="scss">
.s {
    &-title {
        font-family: 'Pretendard Bold';
        font-size: 20px;
        color: #fff;
        line-height: 1.5;
        letter-spacing: -0.5px;
        margin: 16px 0 6px;
    }
    &-sub {
        font-family: 'Pretendard Medium';
        font-size: 14px;
        color: #777e8c;
        line-height: 1.43;
        word-break: keep-all;
    }
    &-notice {
        &-box {
            border-radius: 6px;
            background-color: #eee;
            padding: 7px;
            font-family: 'Pretendard Medium';
            font-size: 14px;
            margin-bottom: 4px;
            color: #777e8c;
            display: flex;
            align-items: center;
        }
        &-icon {
            width: 20px;
            height: 20px;
            margin-right: 4px;
        }
    }
    &-content {
        font-family: 'Pretendard';
        font-size: 24px;
        line-height: 1.5;
        letter-spacing: -0.5px;
        text-align: left;
        color: #777e8c;
        word-break: keep-all;
    }
}

</style>